.pos-card, .table-section {
  background-color: var(--fg-color);
  box-shadow: var(--shadow-base);
  border-radius: var(--border-radius-md);
}

.table-section {
  display: flex;
  flex-direction: column;
  padding: var(--padding-md) var(--padding-lg);
  overflow: visible;
  margin-top: var(--margin-md);
}
.table-section > .table-details {
  display: flex;
  flex-direction: column;
  background-color: var(--fg-color);
}
.table-section > .table-details > .header {
  display: flex;
  margin-bottom: var(--margin-md);
}

.table-number {
  font-size: var(--text-md);
  font-weight: 500;
  color: var(--gray-600);
  margin-top: auto;
}

.pointer-no-select, .primary-action-pos, .print-order-section > .table-order-btn, .print-order-section > .caption-order-btn {
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}

.primary-action-pos, .print-order-section > .table-order-btn, .print-order-section > .caption-order-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--padding-sm);
  margin-top: var(--margin-sm);
  border-radius: var(--border-radius-md);
  font-size: var(--text-lg);
  font-weight: 700;
}

.print-button-order, .print-order-section > .table-order-btn, .print-order-section > .caption-order-btn {
  background-color: var(--gray-300);
  width: 100%;
  color: white;
}

.print-order-section {
  display: flex;
  -moz-column-gap: 1em;
       column-gap: 1em;
}